<template>
	<div>
		<h3>批量应用时间模板</h3> 
        

      <div v-if="tplList.length<=0">       
      尚未创建模板， <router-link to="/bat/timeCreate">点此创建。</router-link>      
      </div>
    

    <div v-for="item in tplList" style="margin-bottom: 10px;"> 
    <el-card class="box-card"  shadow="hover">
      <div slot="header" class="clearfix">
        <span>时间模板：{{item.tpl_name}}</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="deleteTpl(item)"><i class="el-icon-delete"></i>
</el-button>
      </div>
      <div  class="text item tpl_content" @click="editTimeItem(item)">
        {{parseTplData(item.tpl_content)}}
      </div>
      
      <div  class="text item" >
        <el-button type="primary" @click="clickApply(item)">应用到计划</el-button>   <el-button @click="editTimeItem(item)">编辑</el-button>
        
      </div>
    </el-card>
    </div>
    
    
    <ScheduleList ref="ScheduleListComponent" @applyScheduleList="applyScheduleList"></ScheduleList>
    
	</div>
</template>

<script>
  
  import {request} from "@/network/request.js";
  import Qs from 'qs';
  import ScheduleList from "@/components/commen/ScheduleList.vue";
    
  export default {
    data() {
      return {
        
        tplList:[],
        drawer: false,
        direction: 'rtl',
        schedule:'',
      }
    },
    components: {
        ScheduleList: ScheduleList,
    },
    
    methods:{
        getTimeTpl(){
          
          request({
            url:"/subway/getTimeTpl",
            method: 'get',
          
          }).then( res => {            
            console.log(res)            
            this.tplList=res.data;

          })
        },
        getCampaignSchedule() {
          
            request({
              url:"/subway/getCampaignSchedule",
              method: 'get',            
            }).then( res => {
              console.log(res)
              
               this.$refs.ScheduleListComponent.CampaignSchedule =res.data;
               this.$refs.ScheduleListComponent.CampaignScheduleShow =res.data;
            
            })
          
          
        },
        
        deleteTpl(item) {
          
          this.$confirm('是否继续删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  }).then(() => {
                    
                    request({
                      url:"/subway/delTimeTpl",
                      method: 'POST',
                      data:Qs.stringify({
                        id:item.id,
                        job:"delete"
                      }),
                    }).then( res => {            
                      console.log(res)
                      
                      //去掉此条记录
                      this.getTimeTpl();
                    
                    })
                    this.$message({
                      type: 'success',
                      message: '删除成功!'
                    });
                  })
        
        },
        //打开计划选择框
        clickApply(item){
          this.schedule = item.tpl_content;
          this.$refs.ScheduleListComponent.drawer = true;
          
        },
        
        //应用到计划
        applyScheduleList(checkedList)
        {
          
           console.log(checkedList);
          
          for(var i=0;i<checkedList.length;i++)
          {

            request({
              url:"/subway/updateCampaignSchedule",
              method: 'POST',
              data:Qs.stringify({
                campaign_id: checkedList[i],
                schedule:this.schedule,
              }),
            }).then( res => {
              console.log(res)
              if(res.data.data.campaign_schedule.campaign_id)
              {
                let cid = res.data.data.campaign_schedule.campaign_id;
                this.$message({
                  type: 'success',
                  message: cid+'设置成功!'
                });
              } else {
                
                this.$message({
                  type: 'fail',
                  message: '设置失败!'
                });
              }
              
              this.$refs.ScheduleListComponent.applyLoading = false;
              
              
            })
          }

        },
        parseTplData(data)
        {
            
            var d = data.split(";");
            
            var week = ['星期一','星期二','星期三','星期四','星期五','星期六','星期日',]
            var str = '';
            for(var i=0; i< 7; i++)
            {
                str += week[i] + ":"+ d[i]+ "% ";
            }
            
            return str;
        
        },
        
        editTimeItem(item)
        {
            this.$router.push({ path:'/bat/timeCreate', query:{id:item.id}})
        }
        
        
        
    },
    mounted() {
      this.getTimeTpl();
      
      this.getCampaignSchedule();
      
    },
    watch: {
          '$route' (to, from) {
              
              if(from.path == '/bat/timeCreate')
              {
                   this.getTimeTpl();      
                   this.getCampaignSchedule();
              }

          }
    }
      
  }

</script>

<style>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .tpl_content{
      
      padding:10px 0px;
      cursor:pointer;
  }
</style>
